body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.section {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 3rem 1.5rem;
  display: block;
}

.set-gallery {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  list-style-type: none;
}

.hvr-grow {
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
          transform: perspective(1px) translateZ(0);
  -webkit-transition-duration: .3s;
          transition-duration: .3s;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}

.content-image {
  height: 140px;
  text-align: center;
  padding-top: 15px;
  position: relative;
}

.content-image img {
  max-height: 100%;
}

img {
  height: auto;
  max-width: 100%;
}

div {
  display: block;
}

.set {
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(10,10,10,.1), 0 0 0 1px rgba(10,10,10,.1);
  color: #4a4a4a;
  max-width: 100%;
  position: relative;
  display: block;
  margin: 1em;
  padding: 1em;
  -webkit-flex-basis: calc(33.33% - 2em);
          flex-basis: calc(33.33% - 2em);
  min-width: 250px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  cursor: pointer;
  max-width: 305px;
}

audio, img, video {
  height: auto;
  max-width: 100%;
}

* {
  box-sizing: inherit;
}

.image.is-48x48 {
  height: 48px;
  width: 48px;
}

.content ul {
  list-style: disc outside;
  margin-left: 2em;
  margin-top: 1em;
}

blockquote, body, dd, dl, dt, fieldset, figure, h1, h2, h3, h4, h5, h6, hr, html, iframe, legend, li, ol, p, pre, textarea, ul {
  margin: 0;
  padding: 0;
}

.set-gallery .set .media {
  -webkit-align-items: center;
          align-items: center;
}

.media-left {
  margin-right: 1rem;
}

.media-left, .media-right {
  -webkit-flex-basis: auto;
          flex-basis: auto;
  -webkit-flex-grow: 0;
          flex-grow: 0;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.media-content {
  -webkit-flex-basis: auto;
          flex-basis: auto;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  text-align: left;
}

.title.is-4 {
  font-size: 1.5rem;
}

.subtitle.is-6 {
  font-size: 1rem;
}

.subtitle {
  color: #4a4a4a;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 1.25;
}

.subtitle, .title {
  word-break: break-word;
}

image {
  display: block;
  position: relative;
}

.media {
  display: -webkit-flex;
  display: flex;
}

.set-content {
  padding: 1.5rem;
}

.column {
  display: block;
  -webkit-flex-basis: 0;
          flex-basis: 0;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  padding: .75rem;
}

.pkmn-gallery img {
  background: transparent url(/static/media/card-back-small.ce3a314e.png) no-repeat 50%;
  background-size: 95%;
  margin: 10px 20px 10px 0;
  width: 240px;
  cursor: pointer;
}

.hvr-grow:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.pkmn-gallery {
  text-align: center;
}
